
'use client';

import * as React from 'react';
import Link from 'next/link';
import { useRouter } from 'next/navigation'; // Import useRouter
import {
  User,
  Store,
  Phone,
  MapPin,
  Lock,
  Eye,
  EyeOff,
  UploadCloud,
  CheckCircle, // Updated Address Icon
  Loader2, // Added Loader icon
} from 'lucide-react';

import { Button } from '@/components/ui/button';
import { Input } from '@/components/ui/input';
import { Tabs, TabsList, TabsTrigger } from '@/components/ui/tabs'; // Removed TabsContent as it's not used here
import { Separator } from '@/components/ui/separator';
import { cn } from '@/lib/utils';
import { useToast } from '@/hooks/use-toast'; // Import useToast

// --- Simulate Backend Functions (Replace with actual API calls) ---

// Simulate OCR process
const simulateOCR = async (imageFile: File): Promise<{ name: string | null; success: boolean }> => {
    console.log(`Simulating OCR for image: ${imageFile.name}`);
    await new Promise(resolve => setTimeout(resolve, 1500)); // Simulate network delay
    // In reality, send image to OCR service (e.g., Google Cloud Vision)
    // For simulation, randomly succeed or fail, and maybe return a mock name
    const success = Math.random() > 0.2; // 80% success rate
    if (success) {
        // Simulate extracting a name (could be slightly different)
        const mockExtractedName = imageFile.name.includes('front') ? "Mock Name From ID" : null;
        console.log(`OCR Simulation Success. Extracted Name: ${mockExtractedName}`);
        return { name: mockExtractedName, success: true };
    } else {
        console.log("OCR Simulation Failed.");
        return { name: null, success: false };
    }
};

// Simulate user registration in backend database
const simulateRegisterUser = async (userData: any): Promise<{ success: boolean; userId?: string; message: string }> => {
    console.log("Simulating user registration in backend:", userData);
    await new Promise(resolve => setTimeout(resolve, 1000)); // Simulate DB operation
    // In reality, save user data to Firestore/Database (potentially with 'pending_verification' status)
    const success = Math.random() > 0.1; // 90% success rate for DB write
    if (success) {
        const userId = `user_${Date.now()}`; // Simulate generating a user ID
        console.log(`Backend Registration Simulation Success. User ID: ${userId}`);
        return { success: true, userId: userId, message: "User created pending verification." };
    } else {
        console.log("Backend Registration Simulation Failed.");
        return { success: false, message: "Database error during registration." };
    }
};

// Simulate sending SMS
const simulateSendSMS = async (phoneNumber: string, message: string): Promise<{ success: boolean }> => {
    console.log(`Simulating sending SMS to ${phoneNumber}: "${message}"`);
    await new Promise(resolve => setTimeout(resolve, 500)); // Simulate SMS API call
    // In reality, use Twilio, Vonage, etc.
    const success = Math.random() > 0.1; // 90% success rate
    if (success) {
        console.log("SMS Simulation Success.");
    } else {
        console.log("SMS Simulation Failed.");
    }
    return { success };
};

// Simulate backend verification update
const simulateVerifyUser = async (userId: string): Promise<{ success: boolean }> => {
    console.log(`Simulating updating user ${userId} status to verified in backend.`);
    await new Promise(resolve => setTimeout(resolve, 300)); // Simulate DB update
    console.log("User Verification Status Update Simulation Success.");
    return { success: true };
};

// --------------------------------------------------------------------


export default function RegisterPage() {
  const [showPassword, setShowPassword] = React.useState(false);
  const [showConfirmPassword, setShowConfirmPassword] = React.useState(false);
  const [formData, setFormData] = React.useState({
    fullName: '',
    businessActivity: '',
    phoneNumber: '',
    address: '',
    username: '',
    password: '',
    confirmPassword: '',
  });
  const [idType, setIdType] = React.useState('personal-id'); // State for selected ID type
  const [idImageFront, setIdImageFront] = React.useState<File | null>(null);
  const [idImageBack, setIdImageBack] = React.useState<File | null>(null);
  const frontImageRef = React.useRef<HTMLInputElement>(null);
  const backImageRef = React.useRef<HTMLInputElement>(null);
  const [isRegistering, setIsRegistering] = React.useState(false); // Loading state

  const router = useRouter(); // Initialize useRouter
  const { toast } = useToast(); // Initialize useToast

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    setFormData((prev) => ({ ...prev, [name]: value }));
  };

   const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>, side: 'front' | 'back') => {
      if (e.target.files && e.target.files[0]) {
          const file = e.target.files[0];
           // Basic validation (optional: size, type)
           if (file.size > 5 * 1024 * 1024) { // Limit to 5MB
              toast({ title: "خطأ", description: "حجم الصورة كبير جداً (الحد الأقصى 5MB).", variant: "destructive" });
              e.target.value = ''; // Clear the input
              return;
          }
          if (!file.type.startsWith('image/')) {
               toast({ title: "خطأ", description: "الرجاء اختيار ملف صورة.", variant: "destructive" });
               e.target.value = ''; // Clear the input
               return;
           }

          if (side === 'front') {
              setIdImageFront(file);
              console.log('Front image selected:', file.name);
          } else {
              setIdImageBack(file);
              console.log('Back image selected:', file.name);
          }
      }
  };

  const handleRegister = async () => {
    setIsRegistering(true); // Start loading
    console.log('Registration attempt with data:', { ...formData, idType });
    console.log('Front Image:', idImageFront?.name);
    console.log('Back Image:', idImageBack?.name);

    // --- Basic Client-Side Validation ---
    if (formData.password !== formData.confirmPassword) {
      toast({
        title: 'خطأ في التسجيل',
        description: 'كلمتا المرور غير متطابقتين.',
        variant: 'destructive',
      });
      setIsRegistering(false);
      return;
    }
    // Add more checks: password strength, phone number format, etc.
     const phoneRegex = /^7[01378]\d{7}$/; // Example Yemeni mobile format
     if (!phoneRegex.test(formData.phoneNumber)) {
         toast({ title: "خطأ", description: "صيغة رقم الهاتف غير صحيحة.", variant: "destructive" });
         setIsRegistering(false);
         return;
     }

    if (!formData.fullName || !formData.phoneNumber || !formData.username || !formData.password || !idImageFront) {
         toast({
            title: 'خطأ في التسجيل',
            description: 'يرجى ملء جميع الحقول الإلزامية وتحميل صورة الوثيقة الأمامية.',
            variant: 'destructive',
         });
         setIsRegistering(false);
         return;
     }

    // --- Backend Registration & OCR Simulation Flow ---
    try {
        toast({ title: 'جاري معالجة التسجيل...', description: 'يتم رفع الوثائق والتحقق...', variant: 'default' });

        // 1. Simulate backend registration (gets user ID)
        const registerResult = await simulateRegisterUser(formData);
        if (!registerResult.success || !registerResult.userId) {
            throw new Error(registerResult.message || "فشل تسجيل المستخدم في قاعدة البيانات.");
        }
        const userId = registerResult.userId;

        // 2. Simulate OCR on the front ID image
        toast({ title: 'جاري قراءة بيانات الوثيقة...', description: 'قد يستغرق هذا بعض الوقت.', variant: 'default' });
        const ocrResult = await simulateOCR(idImageFront);
        if (!ocrResult.success) {
             // Handle OCR failure - maybe proceed but flag for manual review?
             console.warn("OCR failed, proceeding but flagging for manual review potentially.");
             toast({ title: "تحذير", description: "لم نتمكن من قراءة الوثيقة تلقائياً. سيتم مراجعة حسابك يدوياً.", variant: "default" });
             // For simulation, let's assume manual review means success for now, but no SMS.
             // In reality, update user status to 'pending_manual_review'
             // await simulateUpdateUserStatus(userId, 'pending_manual_review');
             toast({ title: "تم التسجيل", description: "تم إنشاء حسابك وهو قيد المراجعة اليدوية.", variant: "default" });
             router.push('/login'); // Redirect even if OCR fails but registration succeeded
             setIsRegistering(false);
             return;
        }

        // 3. Compare OCR name with formData.fullName (Simple comparison for simulation)
         const ocrNameLower = ocrResult.name?.toLowerCase().trim();
         const inputNameLower = formData.fullName.toLowerCase().trim();
         // Very basic check - real world needs fuzzy matching
         const isMatch = ocrNameLower === inputNameLower || ocrNameLower?.includes(inputNameLower.split(' ')[0]); // Example: check if first name matches

        if (isMatch) {
            // 4. Names match - Mark account as verified and send SMS
            toast({ title: "تم التحقق بنجاح!", description: "الاسم مطابق للوثيقة. جاري إرسال كلمة المرور...", variant: "default" });

             // Simulate updating user status to verified in backend
            await simulateVerifyUser(userId);

            // Simulate sending SMS with password
            const smsMessage = `تم قبول حساب 4NOW الخاص بك. كلمة المرور: ${formData.password}`;
            const smsResult = await simulateSendSMS(formData.phoneNumber, smsMessage);

            if (smsResult.success) {
                toast({ title: "نجاح التسجيل!", description: "تم قبول حسابك وإرسال كلمة المرور إلى هاتفك.", variant: "default" });
            } else {
                toast({ title: "نجاح التسجيل (مشكلة SMS)", description: "تم قبول حسابك ولكن فشل إرسال كلمة المرور عبر SMS. يرجى استخدام 'استعادة كلمة السر'.", variant: "default" });
            }
            router.push('/login'); // Redirect to login

        } else {
            // 5. Names don't match
            console.warn("Name mismatch:", { input: formData.fullName, ocr: ocrResult.name });
            toast({
                title: "فشل التحقق",
                description: "لم يتطابق الاسم في الوثيقة مع الاسم المدخل. سيتم مراجعة حسابك يدوياً.",
                variant: "destructive", // Use destructive to indicate mismatch
            });
             // Update status to 'pending_manual_review' in backend
             // await simulateUpdateUserStatus(userId, 'pending_manual_review');
             router.push('/login'); // Still redirect, but user needs manual approval
        }

    } catch (error: any) {
        console.error("Registration process failed:", error);
        toast({
            title: "خطأ في التسجيل",
            description: error.message || "حدث خطأ غير متوقع أثناء التسجيل.",
            variant: "destructive",
        });
    } finally {
        setIsRegistering(false); // Stop loading
    }
  };

  return (
    // Background: Use theme background
    <div className="flex min-h-screen flex-col items-center bg-background px-4 pt-[32px] text-foreground">
      {/* Status Bar Area (Placeholder) */}
      <div className="h-[24px] w-full"></div>

       {/* Logo Header - Use card for background */}
      <div className="mb-8 flex h-[120px] w-[120px] items-center justify-center rounded-full bg-card shadow-lg">
         <span className="text-3xl font-bold">
           <span className="text-primary">٤</span> {/* Use primary color */}
           <span className="text-accent">Now</span> {/* Use accent color */}
         </span>
      </div>

       {/* Card Container - Use card styles */}
      <div className="w-full max-w-md rounded-[24px] bg-card p-4 shadow-xl text-card-foreground">
        {/* Personal Information Section */}
        <div className="mb-4">
           <div className="flex items-center justify-center space-x-2 rtl:space-x-reverse">
             <Separator className="flex-1 bg-border" /> {/* Use border color */}
             <h2 className="whitespace-nowrap text-lg font-medium text-card-foreground">
               البيانات الشخصية
             </h2>
             <Separator className="flex-1 bg-border" />
           </div>

           <div className="mt-3 space-y-3"> {/* 12px gap */}
            {/* Full Name */}
             <div className="relative">
              <Input
                type="text"
                name="fullName" // Add name attribute
                placeholder="الاسم الرباعي مع اللقب"
                value={formData.fullName}
                onChange={handleInputChange}
                className="h-12 rounded-lg border border-border bg-input pr-10 text-base placeholder:text-muted-foreground text-foreground" // Use theme colors
                dir="rtl"
                disabled={isRegistering}
              />
              <User className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" /> {/* Icon color */}
            </div>
             {/* Business Activity */}
            <div className="relative">
              <Input
                type="text"
                name="businessActivity" // Add name attribute
                placeholder="النشاط التجاري"
                value={formData.businessActivity}
                onChange={handleInputChange}
                className="h-12 rounded-lg border border-border bg-input pr-10 text-base placeholder:text-muted-foreground text-foreground"
                dir="rtl"
                 disabled={isRegistering}
              />
              <Store className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" />
            </div>
             {/* Phone Number */}
            <div className="relative">
              <Input
                type="tel"
                name="phoneNumber" // Add name attribute
                placeholder="رقم الهاتف"
                value={formData.phoneNumber}
                onChange={handleInputChange}
                className="h-12 rounded-lg border border-border bg-input pr-10 text-base placeholder:text-muted-foreground text-foreground"
                dir="rtl"
                 disabled={isRegistering}
              />
              <Phone className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" />
            </div>
             {/* Address */}
            <div className="relative">
              <Input
                type="text"
                name="address" // Add name attribute
                placeholder="العنوان"
                value={formData.address}
                onChange={handleInputChange}
                className="h-12 rounded-lg border border-border bg-input pr-10 text-base placeholder:text-muted-foreground text-foreground"
                dir="rtl"
                 disabled={isRegistering}
              />
              {/* Using CheckCircle as per design image */}
              <CheckCircle className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" />
            </div>
          </div>
        </div>

        {/* Login Details Section */}
        <div className="mb-4">
           <div className="flex items-center justify-center space-x-2 rtl:space-x-reverse">
             <Separator className="flex-1 bg-border" />
             <h2 className="whitespace-nowrap text-lg font-medium text-card-foreground">
               بيانات الدخول
             </h2>
             <Separator className="flex-1 bg-border" />
           </div>

           <div className="mt-3 space-y-3">
            {/* Username */}
            <div className="relative">
              <Input
                type="text"
                name="username" // Add name attribute
                placeholder="اسم الدخول أو رقم الهاتف"
                value={formData.username}
                onChange={handleInputChange}
                className="h-12 rounded-lg border border-border bg-input pr-10 text-base placeholder:text-muted-foreground text-foreground"
                dir="rtl"
                 disabled={isRegistering}
              />
               <User className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" />
            </div>
            {/* Password */}
            <div className="relative">
              <Input
                type={showPassword ? 'text' : 'password'}
                name="password" // Add name attribute
                placeholder="كلمة المرور"
                value={formData.password}
                onChange={handleInputChange}
                className="h-12 rounded-lg border border-border bg-input px-10 text-base placeholder:text-muted-foreground text-foreground" // Padding left and right for icons
                dir="rtl"
                 disabled={isRegistering}
              />
              <Lock className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" />
              <button
                 type="button"
                 onClick={() => setShowPassword(!showPassword)}
                 className="absolute left-3 top-1/2 -translate-y-1/2 transform text-muted-foreground hover:text-foreground" // Adjusted hover color
                 aria-label={showPassword ? "إخفاء كلمة المرور" : "إظهار كلمة المرور"}
                  disabled={isRegistering}
              >
                {showPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
              </button>
            </div>
             {/* Confirm Password */}
             <div className="relative">
              <Input
                 type={showConfirmPassword ? 'text' : 'password'}
                 name="confirmPassword" // Add name attribute
                 placeholder="تأكيد كلمة المرور"
                 value={formData.confirmPassword}
                 onChange={handleInputChange}
                 className="h-12 rounded-lg border border-border bg-input px-10 text-base placeholder:text-muted-foreground text-foreground"
                 dir="rtl"
                  disabled={isRegistering}
              />
              <Lock className="absolute right-3 top-1/2 h-5 w-5 -translate-y-1/2 transform text-muted-foreground" />
               <button
                 type="button"
                 onClick={() => setShowConfirmPassword(!showConfirmPassword)}
                 className="absolute left-3 top-1/2 -translate-y-1/2 transform text-muted-foreground hover:text-foreground"
                 aria-label={showConfirmPassword ? "إخفاء تأكيد كلمة المرور" : "إظهار تأكيد كلمة المرور"}
                  disabled={isRegistering}
              >
                {showConfirmPassword ? <EyeOff className="h-5 w-5" /> : <Eye className="h-5 w-5" />}
              </button>
            </div>
          </div>
        </div>

        {/* Document Type Tabs */}
        {/* Use muted background for the list */}
        <Tabs value={idType} onValueChange={setIdType} className="mt-4 w-full">
           <TabsList className="grid h-auto w-full grid-cols-4 gap-2 bg-muted p-1">
             {/* Active Tab: Use Primary bg, Primary foreground text */}
             {/* Inactive Tab: Use Card bg, Border color, Primary text */}
             <TabsTrigger
              value="personal-id"
               className={cn(
                 "h-10 rounded-lg text-sm font-medium ring-offset-background transition-all focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2",
                 // Active state
                 "data-[state=active]:bg-primary data-[state=active]:text-primary-foreground data-[state=active]:shadow-sm",
                 // Inactive state
                 "data-[state=inactive]:bg-card data-[state=inactive]:text-primary data-[state=inactive]:border data-[state=inactive]:border-primary/50 data-[state=inactive]:hover:bg-primary/10"
              )}
               disabled={isRegistering}
            >
              بطاقة شخصية
            </TabsTrigger>
             {/* Repeat styling for other tabs */}
            <TabsTrigger
              value="passport"
               className={cn(
                 "h-10 rounded-lg text-sm font-medium ring-offset-background transition-all focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2",
                 "data-[state=active]:bg-primary data-[state=active]:text-primary-foreground data-[state=active]:shadow-sm",
                 "data-[state=inactive]:bg-card data-[state=inactive]:text-primary data-[state=inactive]:border data-[state=inactive]:border-primary/50 data-[state=inactive]:hover:bg-primary/10"
              )}
               disabled={isRegistering}
            >
              جواز
            </TabsTrigger>
             <TabsTrigger
              value="commercial-reg"
                className={cn(
                 "h-10 rounded-lg text-sm font-medium ring-offset-background transition-all focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2",
                 "data-[state=active]:bg-primary data-[state=active]:text-primary-foreground data-[state=active]:shadow-sm",
                 "data-[state=inactive]:bg-card data-[state=inactive]:text-primary data-[state=inactive]:border data-[state=inactive]:border-primary/50 data-[state=inactive]:hover:bg-primary/10"
              )}
               disabled={isRegistering}
            >
              سجل تجاري
            </TabsTrigger>
            <TabsTrigger
              value="family-card"
                className={cn(
                 "h-10 rounded-lg text-sm font-medium ring-offset-background transition-all focus-visible:outline-none focus-visible:ring-2 focus-visible:ring-ring focus-visible:ring-offset-2",
                 "data-[state=active]:bg-primary data-[state=active]:text-primary-foreground data-[state=active]:shadow-sm",
                 "data-[state=inactive]:bg-card data-[state=inactive]:text-primary data-[state=inactive]:border data-[state=inactive]:border-primary/50 data-[state=inactive]:hover:bg-primary/10"
              )}
               disabled={isRegistering}
            >
              بطاقة عائلية
            </TabsTrigger>
          </TabsList>
          {/* No TabsContent needed as image upload is always visible */}
        </Tabs>

        {/* Image Upload Placeholders */}
        <div className="mt-3 grid grid-cols-2 gap-3"> {/* 12px gap */}
           {/* Front Image Upload */}
            <div
                className={cn(
                    "relative aspect-square w-full cursor-pointer rounded-lg bg-muted flex flex-col items-center justify-center border-2 border-dashed border-border hover:border-primary/50 hover:bg-muted/80",
                    isRegistering && "cursor-not-allowed opacity-60"
                )}
                onClick={() => !isRegistering && frontImageRef.current?.click()}
            >
                 {idImageFront ? (
                    <img src={URL.createObjectURL(idImageFront)} alt="Preview Front" className="h-full w-full object-cover rounded-lg" />
                 ) : (
                    <>
                         <UploadCloud className="h-10 w-10 text-muted-foreground" />
                         <span className="mt-1 text-xs text-muted-foreground">الوجه الأمامي</span>
                     </>
                 )}
                 <input
                    ref={frontImageRef}
                    type="file"
                    accept="image/*"
                    onChange={(e) => handleFileChange(e, 'front')}
                    className="hidden"
                    aria-label="Upload front ID image"
                    disabled={isRegistering}
                 />
            </div>
            {/* Back Image Upload */}
            <div
                 className={cn(
                    "relative aspect-square w-full cursor-pointer rounded-lg bg-muted flex flex-col items-center justify-center border-2 border-dashed border-border hover:border-primary/50 hover:bg-muted/80",
                    isRegistering && "cursor-not-allowed opacity-60"
                )}
                onClick={() => !isRegistering && backImageRef.current?.click()}
            >
                {idImageBack ? (
                     <img src={URL.createObjectURL(idImageBack)} alt="Preview Back" className="h-full w-full object-cover rounded-lg" />
                ) : (
                     <>
                        <UploadCloud className="h-10 w-10 text-muted-foreground" />
                        <span className="mt-1 text-xs text-muted-foreground">الوجه الخلفي (اختياري)</span>
                     </>
                )}
                 <input
                    ref={backImageRef}
                    type="file"
                    accept="image/*"
                    onChange={(e) => handleFileChange(e, 'back')}
                    className="hidden"
                    aria-label="Upload back ID image"
                     disabled={isRegistering}
                 />
            </div>
        </div>

         {/* Register Button - Use primary color */}
        <Button
           className="mt-4 h-12 w-full rounded-lg bg-primary text-base font-medium text-primary-foreground hover:bg-primary/90 active:bg-primary/80" // Use theme colors
           onClick={handleRegister} // Use the handler function
           disabled={isRegistering}
        >
          {isRegistering ? (
             <Loader2 className="mr-2 h-4 w-4 animate-spin" />
          ) : null}
          {isRegistering ? 'جاري التسجيل...' : 'تسجيل'}
        </Button>

        {/* Login Link */}
        <p className="mt-3 text-center text-sm font-light text-muted-foreground"> {/* Use muted-foreground */}
          هل لديك حساب؟{' '}
          <Link href="/login" passHref>
            {/* Use primary link color */}
            <span className={cn("cursor-pointer font-medium text-primary hover:underline", isRegistering && "pointer-events-none opacity-50")}>
              قم بالدخول
            </span>
          </Link>
        </p>

      </div>

       {/* Footer */}
      <footer className="mt-auto pb-4 pt-6 text-center text-xs font-light text-primary-foreground/70"> {/* Slightly muted foreground */}
         {/* Removed contact info */}
         برمجة وتصميم (يمن روبوت)
      </footer>
    </div>
  );
}

